<template>
	<div v-loading.fullscreen.lock="loading" class="QAHead" v-my-directive :data-title="title" data-description="学习技能、解决问题学习技能、解决问题学习技能、解决问题学习技能、解决问题学习技能、解决问题">
		<div class="paddingtop10 columnnav Horizontalspacing minixgap fonfff minfont">
			<el-breadcrumb separator-class="el-icon-arrow-right">
				<el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
				<el-breadcrumb-item v-for="(item, index) in nav" :key="index" :to="{ path: '/course/' + item.alias }">{{ item.name }}</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<div class="questionnav line">
			<div class="columnLayout interval">
				<span class="fonfff center QAfont">{{ current.name }}</span>
				<div class="Horizontalspacing interval fonfff">
					<div class="Horizontalspacing minxgap">
						<div class="iconfont icon-duigouxiao fonfff minfont center"></div>
						<div>学习技能、解决问题学习技能、解决问题学习技能、解决问题学习技能、解决问题学习技能、解决问题</div>
					</div>
				</div>
			</div>
		</div>
	</div>
	<!-- 行内 -->
	<div class="coursenav">
		<div class="Genericpadding content">
			<div class="Horizontalspacing interval">
				<span class="center">分类：</span>
				<el-button plain text class="coreqzsolld" @click=";(curPage = 1), (Classificationtags = '全部'), getdata(current.alias)">全部</el-button>
				<el-button plain text class="coreqzsolld" v-for="(item, index) in subordinate" :key="index" @click=";(curPage = 1), (Classificationtags = item.name), getdata(item.alias)">{{ item.name }}</el-button>
			</div>
			<div class="Horizontalspacing interval">
				<span class="center">价格：</span>
				<el-button plain text class="coreqzsolld" @click=";(Price = null), (pricetag = '全部'), getdata(alias)">全部</el-button>
				<el-button plain text class="coreqzsolld" @click=";(Price = 1), (pricetag = '收费'), getdata(alias)">收费</el-button>
				<el-button plain text class="coreqzsolld" @click=";(Price = 0), (pricetag = '免费'), getdata(alias)">免费</el-button>
			</div>
			<div class="Horizontalspacing interval">
				<span class="center">条件：</span>
				<el-tag v-if="Classificationtags == '全部' && pricetag == '全部'" type="info">无</el-tag>
				<el-tag v-if="Classificationtags != '全部'" closable size="large" @close=";(Classificationtags = '全部'), getdata('')">分类: {{ Classificationtags }}</el-tag>
				<el-tag v-if="pricetag != '全部'" closable size="large" @close=";(Price = null), (pricetag = '全部'), getdata(alias)">价格: {{ pricetag }}</el-tag>
			</div>
		</div>
	</div>
	<!-- 列表 -->
	<div class="coursenav">
		<div v-if="arclist.length == 0" class="dispaygrid">
			<div class="center">
				<el-alert class="center" title="未查询到任何课程" type="error" :closable="false" show-icon />
			</div>
		</div>
		<el-row v-else :gutter="20">
			<el-col class="my-custom-class" :span="6" v-for="(item, index) in arclist" :key="index">
				<el-card :body-style="{ padding: '0px' }">
					<router-link class="atop" :to="/course/ + item.alias + '/' + item.id">
						<div :class="item.saleperiod ? 'TopRectangles' : ''">
							<div v-if="item.saleperiod" class="saleperiodabc"><span class="center">限时优惠</span></div>
							<el-image class="demo-image__error" :src="item.thumbnail">
								<template #error>
									<div class="image-slot">
										<el-icon><Picture /></el-icon>
									</div>
								</template>
							</el-image>
						</div>
					</router-link>

					<div style="padding: 14px" class="minfont ThinLineLayout minaxgap">
						<div class="Horizontalspacing minxgap">
							<el-tag effect="plain" size="small">{{ item.pidname }}</el-tag>
							<!-- <el-tag effect="plain">{{ item.saleperiod ? '限时' : '' }}{{ item.saleperiod ? (item.discount > 0 ? '$' + item.discount : '免费') : item.Price > 0 ? '$' + item.Price : '免费' }}</el-tag> -->
							<el-tooltip class="box-item" effect="light" :content="item.title" placement="top-start">
								<router-link class="center" :to="/course/ + item.alias + '/' + item.id">
									<div class="coreqzsolld truncate-text">{{ item.title }}</div>
								</router-link>
							</el-tooltip>
						</div>
						<div class="custom-font-size" :style="!item.saleperiod ? { height: '24px !important' } : {}">
							<el-tag v-if="item.saleperiod" type="danger" effect="plain"><el-countdown format="[优惠剩余]D[天]HH[时]mm[分]ss[秒]" :value="parseInt(item.DiscountEnd)" :value-style="{ fontSize: '10px' }" /></el-tag>
						</div>
						<div class="Horizontalspacing space-around">
							<router-link :to="/homepage/ + item.user.id">
								<div class="Horizontalspacing minxgap">
									<el-avatar class="center" :src="item.user.Avatar" :size="20">
										<el-icon>
											<Edit />
										</el-icon>
									</el-avatar>
									<div class="minfont grey center coreqzsolld truncate-texts">{{ item.user.nickname ? item.user.nickname : item.user.username }}</div>
								</div>
							</router-link>
							<div class="Horizontalspacing minxgap coreplice">
								<span class="center">{{ item.saleperiod ? (item.discount > 0 ? '$' + item.discount : '免费') : item.Price > 0 ? '$' + item.Price : '免费' }}</span>
								<el-text v-if="item.saleperiod" tag="del">原价:{{ item.Price }}</el-text>
							</div>
							<router-link :to="/course/ + item.alias + '/' + item.id"><el-button type="danger" size="small">学习</el-button></router-link>
						</div>
					</div>
				</el-card>
			</el-col>
		</el-row>
		<div class="dispaygrid">
			<el-pagination background class="center" :hide-on-single-page="value" @current-change="page" :current-page="curPage" :page-size="pagesize" layout="prev, pager, next" :total="total"> </el-pagination>
		</div>
	</div>
</template>
<script>
import router from '../router' // 导入Vue Router
import left from './../components/left'
import { dataRequest as api } from '@/api/api'
export default {
	name: 'name',
	components: {
		// 组件
		left,
	},

	data() {
		return {
			title: '所有课程',
			comn: this.$route.params.coureid,
			arclist: [],
			nav: [],
			current: [],
			Price: null,
			alias: '',
			subordinate: [],
			loading: true,
			Classificationtags: '全部',
			pricetag: '全部',

			qzsolldav: `qzsolldav`,
			tesorting: 0,
			order: false,

			//返回值
			value: true,
			pagesize: 20, //每页显示数量
			curPage: 1, // 当前页数
			total: 0, // 一共几条数据
			offset: 'DESC',
			// 默认按照时间
			tosort: 'time',
		}
	},
	watch: {
		'$route.params.coureid': function (newVal, oldVal) {
			// 当coureid参数发生变化时触发
			this.comn = newVal // 更新comn的值
			this.get() // 重新获取数据
		},
	},
	methods: {
		handleClose(tag) {
			this.getdata('')
		},
		// 获取数据
		async get() {
			this.loading = true
			console.log('comn', this.comn)
			let comn = await api(['post=Courselevel', this.comn])
			if (comn.code == 200) {
				if (comn.data['nav'][0]) {
					comn.data['nav'].unshift({ id: '0', name: '全部课程', alias: '', pid: '0', level: '0' })
				} else {
					comn.data['nav'] = [{ id: '0', name: '全部课程', alias: '', pid: '0', level: '0' }]
				}

				this.nav = comn.data['nav']
				console.log(this.nav)
				this.subordinate = comn.data['Classification']
				this.current = this.nav[this.nav.length - 1]
			}
			this.getdata(this.comn)
		},

		async getdata(alias) {
			this.loading = true
			this.alias = alias
			let array = await api(['post=Coursepagination', { perPage: this.pagesize, page: this.curPage, offset: this.offset, tosort: this.tosort, alias: this.alias, Price: this.Price }])
			console.log(array)
			if (array.code == 200) {
				this.total = array.massage / 1
				this.arclist = array.data
				let arr = []
				for (let i = 0; i < this.arclist.length; i++) {
					arr.push(this.arclist[i].id)
				}
				this.PageView(arr)
				console.log(arr)
			} else if (array.code == 404) {
				this.total = 0
				this.arclist = []
			}
			this.loading = false
		},
		// 分页
		async page(ele = 1) {
			window.scrollTo({ behavior: 'instant', top: 400 })
			this.curPage = ele
			this.getdata(this.alias)
		},
		// 浏览量
		PageView(ele) {
			api(['post=corebrowsing', ele])
		},
	},

	mounted() {
		// 生命周期:
		this.get()
	},
	updated() {
		// this.loading = false
	},
}
</script>
<style scoped>
.coursenav {
	width: 1200px;
	padding-top: 20px;
	margin: auto;
}
.my-custom-class {
	margin-bottom: 20px; /* 设置行间距为 20 像素 */
}
.el-image {
	width: 283px;
	height: 283px;
}
.demo-image__error .el-image {
	padding: 0 5px;
	width: 100%;
	height: 100%;
}

.demo-image__error .image-slot {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100%;
	background: var(--el-fill-color-light);
	color: var(--el-text-color-secondary);
	font-size: 30px;
}
.demo-image__error .image-slot .el-icon {
	font-size: 30px;
}
.courepage {
	margin: auto;
}
.coreqzsolld:hover {
	color: var(--primary-color);
}
.saleperiodabc {
	position: absolute;
	display: grid;
	font-size: 16px;
	top: 0px;
	height: 24px;
	width: 65px;
	border-bottom-right-radius: 4px;
	background: #f56c6c; /* 使用 rgba 表示法，最后一个值 0.5 表示透明度，取值范围为 0 到 1 */
	z-index: 100;
}
.saleperiodabc span {
	font-weight: 550;
	font-size: 10px;
	color: rgb(255, 255, 255);
}
.atop {
	height: 0px !important;
	font-size: 0px;
}
.coreplice {
	font-weight: bold;
	font-size: 12px;
	color: #ff485e !important;
}
.minfont {
	font-size: 12px;
}
.truncate-text {
	width: 187px !important;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	-webkit-text-overflow: ellipsis; /* Webkit浏览器前缀 */
}
.truncate-texts {
	width: 50px !important;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	-webkit-text-overflow: ellipsis; /* Webkit浏览器前缀 */
}
</style>
<style>
.custom-font-size .el-statistic__content {
	width: 100%;
	font-weight: inherit; /* 清除字体粗细 */
	font-size: inherit; /* 清除字体大小 */
	color: inherit;
}
.coreplice .el-text {
	font-weight: 400 !important;
	font-size: 12px;
}
</style>
